import React, { Component } from 'react';
import firebase from '../../../firebase';
import ContentWrapper from '../Layout/ContentWrapper';
import { Row, Col, Dropdown, DropdownMenu, DropdownToggle, DropdownItem, Button } from 'reactstrap';
import swal from 'sweetalert'
import axios from 'axios';
import { url_managers } from '../../../urls';
import { signOut } from '../../Authentication/AuthFunction';

class ContactDetails extends Component {
    constructor(props) {
        super(props)
        this.state = {
        manager: {},
            dropdownOpen: false,
        }
    }

    toggle = () => {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    componentDidMount() {
        firebase.auth().onAuthStateChanged( (authUser) => {
            if(authUser) {
                console.log(authUser.uid)
                const uid = authUser.uid
                axios.get(`${url_managers}${uid}`)
                .then((res) => {
                    this.setState({ manager : res.data.data})
                })
                .catch((error) => {
                    console.log('Erreur', error)
                    swal('Erreur', 'Vous ne pouvez pas accéder à cette page pour le moment, réessayer ultérieurement', 'error')
                })
            }
            else {
                this.props.history.push('/login')
            }
        })
    }

    logOut() {
        signOut()
        .catch((error) => {
            console.log('Un problème est survenu')
        })
    }

    render() {
        const { nom, prenom, email, role, username, adresse, telephone, cni, nationalite} = this.state.manager

        return (
            <ContentWrapper>
                <Row>
                    <Col lg="4">
                        <div className="card card-default">
                            <div className="card-body text-center">
                                <div className="py-4">
                                    <img className="img-fluid rounded-circle img-thumbnail thumb96" src="img/user/02.jpg" alt="Contact"/>
                                </div>
                                <h3 className="m-0 text-bold"> {username} </h3>
                                <div className="my-3">
                                    <p>Hello, Have fun!</p>
                                </div>
                            </div>
                        </div>
                        <div className="card card-default d-none d-lg-block">
                            <div className="card-header">
                                <div className="card-title text-center">Recent contacts</div>
                            </div>
                            <div className="card-body">
                                <div className="media">
                                    <img className="align-self-center mr-2 rounded-circle img-thumbnail thumb48" src="img/user/04.jpg" alt="Contact"/>
                                    <div className="media-body py-2">
                                        <div className="text-bold">Floyd Ortiz
                                            <div className="text-sm text-muted">12m ago</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="media">
                                    <img className="align-self-center mr-2 rounded-circle img-thumbnail thumb48" src="img/user/05.jpg" alt="Contact"/>
                                    <div className="media-body py-2">
                                        <div className="text-bold">Luis Vasquez
                                            <div className="text-sm text-muted">2h ago</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="media">
                                    <img className="align-self-center mr-2 rounded-circle img-thumbnail thumb48" src="img/user/06.jpg" alt="Contact"/>
                                    <div className="media-body py-2">
                                        <div className="text-bold">Duane Mckinney
                                            <div className="text-sm text-muted">yesterday</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="media">
                                    <img className="align-self-center mr-2 rounded-circle img-thumbnail thumb48" src="img/user/07.jpg" alt="Contact"/>
                                    <div className="media-body py-2">
                                        <div className="text-bold">Connie Lambert
                                            <div className="text-sm text-muted">2 weeks ago</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Col>
                    <Col lg="8">
                        <div className="card card-default">
                            <Button className="ml-auto" onClick={this.logOut} color="success"> Se deconnecter </Button>
                            <div className="card-header d-flex align-items-center">
                                <div className="d-flex justify-content-center col">
                                    <div className="h4 m-0 text-center"> Information</div>
                                </div>
                                <div className="d-flex justify-content-end" hidden>
                                    <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                                        <DropdownToggle color="link">
                                            <em className="fa fa-ellipsis-v fa-lg text-muted"></em>
                                        </DropdownToggle>
                                        <DropdownMenu className="dropdown-menu-right-forced animated fadeInLeft">
                                            <DropdownItem>
                                                <span>Send by message</span>
                                            </DropdownItem>
                                            <DropdownItem>
                                                <span>Share contact</span>
                                            </DropdownItem>
                                            <DropdownItem>
                                                <span>Block contact</span>
                                            </DropdownItem>
                                            <DropdownItem>
                                                <span className="text-warning">Delete contact</span>
                                            </DropdownItem>
                                        </DropdownMenu>
                                    </Dropdown>
                                </div>
                            </div>
                            <div className="card-body">
                                <div className="row py-4 justify-content-center">
                                    <div className="col-12 col-sm-10">
                                        <form className="form-horizontal">
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact1"> Nom </label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <input className="form-control" id="inputContact1" type="text" placeholder="" defaultValue={nom}/>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact2">Email</label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <input className="form-control" id="inputContact2" type="email" defaultValue={email}/>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact3">Numéro de téléphone</label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <input className="form-control" id="inputContact3" type="text" defaultValue={telephone}/>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact4"> Adresse </label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <input className="form-control" id="inputContact4" type="text" defaultValue={adresse}/>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact5">Nationalite</label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <input className="form-control" id="inputContact5" type="text" defaultValue={nationalite}/>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="inputContact6">Numéro de CNI</label>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <textarea className="form-control" id="inputContact6" row="4" defaultValue={cni}> 
                                                    </textarea>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <div className="col-xl-2 col-md-3 col-4"></div>
                                                <div className="col-xl-10 col-md-9 col-8">
                                                    <div className="custom-control custom-checkbox">
                                                        <input className="custom-control-input" id="customCheck1" type="checkbox"/>
                                                        <label className="custom-control-label" htmlFor="customCheck1">Favorite contact?</label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <div className="col-md-10">
                                                    <button className="btn btn-info" type="submit">Update</button>
                                                </div>
                                            </div>
                                        </form>
                                        <div className="text-right">
                                            <a className="text-muted" href="">Delete this contact?</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Col>
                </Row>
            </ContentWrapper>
            );
    }

}

export default ContactDetails;
